.ava{
    width: 80px;
    border-radius: 50px;
}
.ava:hover{
    cursor: grab;
}
.block{
    padding: 30px 30px;
}
.follow{
    width: 100px;
    height: 40px;
    padding: 10px 10px;
    background-color: #18608a;
    color: aliceblue;
    border-radius: 5%;
}
.unfollow{
    width: 100px;
    height: 40px;
    padding: 10px 10px;
    background-color: #6694af;
    color: aliceblue;
    border-radius: 5%;
}
.user {
    margin-bottom: 40px;
    border: 1px solid black;
    padding: 20px 20px;
    background-color: #f5f1f1;
    display: flex;
}

.buttonGet{
    position: relative;
    left: 50%;
    transform: translate(-50%, 0);
    margin-bottom: 30px;
    padding: 10px 10px;
    background-color: #30b8a1;
    font-size: 20px;
}

