.form{
    border: 1px solid black;
    width: 300px;
    height: 400px;
    padding: 50px 30px;
    text-align: center;
    margin: 0 auto;
    margin-top: 100px;
    background-color: #c4d9ec;
}
.login{
    margin-bottom: 10px;
    width: 200px;
    height: 30px;
    border-radius: 5px;
}
.pass{
    margin-bottom: 10px;
    width: 200px;
    height: 30px;
    border-radius: 5px;
}
.check{
    width: 20px;
    height: 15px;
}
.btn{
    margin-top: 10px;
    width: 100px;
    height: 30px;
    background-color: #b1b8f8;
}
.formSummaryError {
    border: red 1px solid;
    padding: 5px;
    color: #ca1c1c;
}
