.selectedPage{
    font-weight: bold;
    
}
.buttonPage{
    margin: 10px;
    width: 90px;
    background-color: #c9d0fa;
}
.pages{
    font-size: 25px;
    text-align: center;
    margin-bottom: 20px;
}
.pages span{
    border: 0.5px solid black;
    margin-right: 5px;
    border-radius: 0%;
    padding: 5px 10px;
    cursor: pointer;
    background-color: #98c5e9;
}
.pages:hover{
    cursor: default;
}